<template>
	<view>
		<!-- ÊêúÁ¥¢Ê†è -->
		<uni-search-bar @click.native="search" placeholder="Type to search" cancelButton="none" :showCase="showCase"></uni-search-bar>
		<!-- È°∂ÈÉ®ÈÄâÈ°πÂç° -->
		<scroll-view scroll-x :scroll-into-view="scrollInto"
		scroll-with-animation
		class="scroll-row border-bottom" 
		style="height: 120rpx;">
			<view v-for="(item,index) in tabBars":key="index"
			class="scroll-row-item px-5 py-2 font-md":id = "'tab'+index"
			:class="tabIndex === index?'text-main font-lg font-weight-bold' : ''"
			@click="changeTab(index)">{{item.name}}</view>
		</scroll-view>

		 

		
		<swiper :duration="500":current="tabIndex" @change="onChangeTab"
		:style="'height:'+scrollH+'px;'">
			<swiper-item v-for="(item,index) in newsList":key="index">
				<scroll-view scroll-y="true":style="'height:' + scrollH + 'px;'">
					<template v-if="item.list.length > 0">
						<!-- ÂàóË°® -->
						<block v-for="(item2, index2) in item.list" :key="index2">
							<!-- ÂàóË°®Ê†∑Âºè -->
							<common-list :item="item2" :index="index2" @follow="follow" @doSupport="doSupport"></common-list>
							<!-- ÂÖ®Â±ÄÂàÜÂâ≤Á∫ø -->
							<divider></divider>
						</block>
						<!-- ‰∏äÊãâÂä†ËΩΩ -->
						<load-more :loadmore ="item.loadmore" ></load-more>
					</template>
					<!-- Êó†Êï∞ÊçÆ -->
					<template v-else>
						<no-thing></no-thing>
					</template>
					
					
				</scroll-view>
				
			</swiper-item>
		</swiper>
		
		<uni-fab horizontal="right" vertical="bottom" :pattern="fabPattern" @fabClick="addInput"></uni-fab>

	</view>
</template>

<script>
	const demo = [{
		username:"ÂÜØËØ∫‰æùÊõºÂçöÂ£´",
		userpic:"/static/Dr. Von Neumann.jpeg",
		newstime:"Êó•Êúü Êó∂Èó¥",
		isFollow:false,
		title:"ÁéãÂ≠êÊÆø‰∏ãÂçàËå∂È§êÂéÖÂäõËÄÉËØï",
		titlepic:"/static/Dr. Von Neumann.jpeg",
		support:{
			type:"support",
			support_count:1,
			unsupport_count:1
		},
		comment_count:2,
		share_num:2,
		images:["/static/Dr. Von Neumann.jpeg","https://bjetxgzv.cdn.bspapp.com/VKCEYUGU-uni-app-doc/6acec660-4f31-11eb-a16f-5b3e54966275.jpg"],
		content:"ÊàëÂ•ΩÁà±Áå´Âí™üêà"
	},
	{
		username:"ÂÜØËØ∫‰æùÊõºÂçöÂ£´",
		userpic:"/static/Dr. Von Neumann.jpeg",
		newstime:"Êó•Êúü Êó∂Èó¥",
		isFollow:false,
		title:"ËøôÊòØ‰∏ÄÂè™Áå´Âí™",
		titlepic:"/static/Dr. Von Neumann.jpeg",
		support:{
			type:"support",
			support_count:1,
			unsupport_count:1
		},
		comment_count:2,
		share_num:2
	},
	{
		username:"ÂÜØËØ∫‰æùÊõºÂçöÂ£´",
		userpic:"/static/Dr. Von Neumann.jpeg",
		newstime:"Êó•Êúü Êó∂Èó¥",
		isFollow:false,
		title:"ËøôÊòØ‰∏ÄÂè™Áå´Âí™",
		titlepic:"/static/Dr. Von Neumann.jpeg",
		support:{
			type:"",
			support_count:1,
			unsupport_count:1
		},
		comment_count:2,
		share_num:2,
	}];
	
	
	import commonList from "@/components/common/common-list.vue";
	import loadMore from '@/components/common/load-more.vue';
	import uniSearchBar from "@/components/uni-ui/uni-search-bar/uni-search-bar.vue"
	import uniFab from "@/components/uni-ui/uni-fab/uni-fab.vue"
	import uniStatusBar from "@/components/uni-ui/uni-nav-bar/uni-status-bar.vue"
	export default {
		components:{
			commonList,
			loadMore,
			uniSearchBar,
			uniFab,
			uniStatusBar
		},
		data() {
			return {
				fabPattern:{
					buttonColor: "#7b5aa6"
				},
				//ÂàóË°®È´òÂ∫¶
				scrollH: 600,
				//È°∂ÈÉ®ÈÄâÈ°πÂç°
				scrollInto: "",
				tabIndex:0,
				tabBars:[{
					name:'ÂÖ≥Ê≥®',
				},{
					name:'ÂÖ≥Ê≥®',
				},{
					name:'ÂÖ≥Ê≥®',
				},{
					name:'ÂÖ≥Ê≥®',
				},{
					name:'ÂÖ≥Ê≥®',
				},{
					name:'ÂÖ≥Ê≥®',
				}],
				newsList:[{
							
					}],
				showCase: false
					
				
			}
		},
		//ÁõëÂê¨ÁÇπÂáªÂØºËà™Ê†èÊêúÁ¥¢Ê°Ü
		onLoad() {
			uni.getSystemInfo({
				success: res => {
					this.scrollH = res.windowHeight - uni.upx2px(101)
				}
				
			})
			//Ê†πÊçÆÈÄâÈ°πÁîüÊàêÂàóË°®
			this.getData()
			

		},
		methods: {
			addInput(){
				uni.navigateTo({
					url:'../add-input/add-input'
				})
			},
			//Ëé∑ÂèñÊï∞ÊçÆ
			getData(){
				var arr = []
				for (let i = 0; i < this.tabBars.length; i++) {
					//Áîü‰∫ßÂàóË°®Ê®°Êùø
					let obj = {
						//1.‰∏äÊãâÂä†ËΩΩÊõ¥Â§ö 2.Âä†ËΩΩ‰∏≠ 3. Ê≤°ÊúâÊõ¥Â§ö‰∫Ü
						loadmore:"‰∏äÊãâÂä†ËΩΩÊõ¥Â§ö",
						
						list:[]
					}
					if (i < 2) {
						obj.list = demo
					}
					
					arr.push(obj)
				}
				this.newsList = arr
				
			},
			//ÁõëÂê¨ÊªëÂä®
			onChangeTab(e){
				this.changeTab(e.detail.current)
			},


			follow(e){
				this.list[e].isFollow = !this.list[e].isFollow
			},
			doSupport(e){
				let item = this.list[e.index]
				if (item.support.type === ""){
					item.support.type = e.type
					item.support[e.type+'_count']++
					return;
				}
				if (item.support.type === "support" && e.type === "unsupport"){
					item.support.type = e.type;
					item.support.support_count--;
					item.support.unsupport_count++;
					
				} else if (item.support.type === "unsupport" && e.type === "support"){
					item.support.type = e.type;
					item.support.unsupport_count--;
					item.support.support_count++;
				}
				else if (item.support.type === e.type){
					item.support.type = "";
					item.support[e.type+'_count']--;
				}
				
				console.log(e.type)
			},

			//ÂàáÊç¢È°∂ÈÉ®ÂØºËà™ÈÄâÈ°π
			changeTab(index){
				if (this.tabIndex === index){
					return;
				}
				this.tabIndex = index
				//ÊªöÂä®Âà∞ÊåáÂÆöÂÖÉÁ¥†
				this.scrollInto = 'tab' + index

			},
			//‰∏äÊãâÂä†ËΩΩÊõ¥Â§ö
			loadmore(index){
				
				//ÂæóÂà∞ÂΩìÂâçÂàóË°®Êï∞ÊçÆ
				let item = this.newsList[index]
				//Âà§Êñ≠ÊòØÂê¶Â§Ñ‰∫éÂèØÂä†ËΩΩÁä∂ÊÄÅ
				if 	(item.loadmore !== '‰∏äÊãâÂä†ËΩΩÊõ¥Â§ö') return;			
				//‰øÆÊîπÂΩìÂâçÂàóË°®Âä†ËΩΩÁä∂ÊÄÅ
				item.loadmore = 'Âä†ËΩΩ‰∏≠...'
				//Êï∞ÊçÆËØ∑Ê±Ç
				setTimeout(()=>{
					//Âä†ËΩΩÈ°µÈù¢ÁöÑÊï∞ÊçÆ
					item.list = [...item.list,...item.list]
					//ÊÅ¢Â§çÂä†ËΩΩÁä∂ÊÄÅ
					item.loadmore = '‰∏äÊãâÂä†ËΩΩÊõ¥Â§ö'
				},10000)
			},
			search(){
				uni.navigateTo({
					url: '../search/search?type=post',
				})
			}
		}
	}
</script>

